body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.snow-fall {
  width: 100%;
  height: 100vh;
  background-image: url("https://i.imgur.com/BiSmXaq.png"),
    url("https://i.imgur.com/XHuy0NJ.png"),
    url("https://i.imgur.com/okpRxJU.png");
  -webkit-animation: snow 20s linear infinite;
          animation: snow 20s linear infinite;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

@-webkit-keyframes snow {
  0% {
    background-position: 0px 0px, 0px 0px, 0px 0px;
  }

  100% {
    background-position: 500px 1000px, 400px 400px, 300px 300px;
  }
}

@keyframes snow {
  0% {
    background-position: 0px 0px, 0px 0px, 0px 0px;
  }

  100% {
    background-position: 500px 1000px, 400px 400px, 300px 300px;
  }
}

header {
  width: 100vw;
  height: 5rem;
  background-color: #000;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2000;
}

.logo-container {
  width: 3rem;
  height: 3rem;
  position: relative;
  left: 0.5rem;
  top: 50%;
  transform: translateY(-50%);
  background: linear-gradient(45deg, #688cb8, #1175f7);
  border-radius: 50%;
}

.logo {
  width: 100%;
  height: 100%;
  position: relative;
  left: -0.2rem;
  top: 0.4rem;
}

@media (min-width: 1024px) {
  .logo-container {
    width: 4rem;
    height: 4rem;
  }
}

nav {
  background: blue;
  width: 12rem;
  position: absolute;
  right: -12rem;
  top: 55vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

nav a {
  font-size: 2rem;
  font-weight: 600;
  text-shadow: 0 0 2px #000;
  margin: 0.4rem 0;
  position: relative;
  -webkit-animation-duration: 500ms;
          animation-duration: 500ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  transition: 400ms;
}

nav a:nth-child(2) {
  -webkit-animation-delay: 150ms;
          animation-delay: 150ms;
}

nav a:nth-child(3) {
  -webkit-animation-delay: 300ms;
          animation-delay: 300ms;
}

nav a:nth-child(4) {
  -webkit-animation-delay: 450ms;
          animation-delay: 450ms;
}

nav a::before {
  content: "";
  width: 0;
  height: 3px;
  background-image: linear-gradient(to left, #314d5c, #114e66);
  position: absolute;
  top: 100%;
  left: 0;
  transition: 100ms;
}

nav a:active::before {
  width: 100%;
}

@-webkit-keyframes slideIn {
  0% {
    right: -12rem;
  }
  100% {
    right: 12rem;
  }
}

@keyframes slideIn {
  0% {
    right: -12rem;
  }
  100% {
    right: 12rem;
  }
}

@media (min-width: 1024px) {
  nav a::before {
    transition: 300ms;
  }
  nav a:hover::before {
    width: 100%;
  }
}

.hamburger {
  cursor: pointer;
  background-color: #000;
  width: 3rem;
  height: 3rem;
  border: 2px solid #fff;
  border-radius: 6px;
  position: absolute;
  right: 2rem;
  top: 1rem;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0.2rem;
}

.hamburger span {
  width: 90%;
  height: 6px;
  background-color: #fff;
  margin: 4px 0;
  position: relative;
  top: 0;
  left: 0;
  transition: 300ms;
  transform: rotate(0);
  border-radius: 40px;
}

@media (min-width: 1024px) {
  .hamburger {
    width: 3.5rem;
    height: 3.5rem;
  }
  .hamburger span {
    margin: 6px 0;
  }
}

.landing {
  width: 100vw;
  height: 100vh;
  background-color: #111;
  background-position: center;
  background-size: cover;
  color: #f5f5f5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom-right-radius: 25rem;
  position: relative;
}

.landing .img-container {
  width: 10rem;
  height: 10rem;
  position: absolute;
  top: 6.5rem;
  left: 50%;
  transform: translateX(-50%);
  background: #2f96be;
  z-index: 99999;
  border-radius: 50%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: 5px solid #114e66;
  box-shadow: 0 0 10px #2f96be;
}

.landing .img-container img {
  width: 90%;
  height: 100%;
  border-radius: 44%;
  position: relative;
}

.landing .landing-title {
  width: 70%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 40%;
}

.landing .landing-title h1 {
  width: 15rem;
  font-family: cursive;
  font-size: 2.7rem;
  margin: 0 auto;
  position: relative;
}

.landing .landing-title h1::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #111;
  -webkit-animation: typeWriterAnimation 1.5s forwards;
          animation: typeWriterAnimation 1.5s forwards;
}

.landing .landing-title h3 {
  width: 15rem;
  font-family: monospace;
  font-size: 2rem;
  margin: 0.5rem auto 2rem auto;
  position: relative;
}

.landing .landing-title h3::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #111;
  -webkit-animation: typeWriterAnimation 1.5s forwards;
          animation: typeWriterAnimation 1.5s forwards;
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}

@-webkit-keyframes typeWriterAnimation {
  0% {
    width: 100%;
  }
  30% {
    width: 100%;
  }
  100% {
    width: 0;
  }
}

@keyframes typeWriterAnimation {
  0% {
    width: 100%;
  }
  30% {
    width: 100%;
  }
  100% {
    width: 0;
  }
}

.landing-btn {
  width: 10rem;
  height: 3rem;
  margin: 0.2rem auto;
  font-family: monospace;
  font-size: 1.5rem;
  font-weight: 800;
  padding: 1rem;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  transition: 400ms;
  overflow: hidden;
  border-radius: 4px;
  position: relative;
  transform: scale(0);
  -webkit-animation: scale 0.7s forwards;
          animation: scale 0.7s forwards;
  -webkit-animation-delay: 2.3s;
          animation-delay: 2.3s;
}

@-webkit-keyframes scale {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes scale {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

.landing-btn:hover {
  transform: scale(1.2);
  background: #2f96be;
  box-shadow: 0 0 15px #2f96be;
}

.landing-btn span {
  transition: 500ms;
}

.landing-btn span:nth-child(1) {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(to right, #0c002b, #2f96be);
  -webkit-animation: landingBtnA1 2s linear infinite;
          animation: landingBtnA1 2s linear infinite;
  box-shadow: 5px 0 10px #2f96be;
}

@-webkit-keyframes landingBtnA1 {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

@keyframes landingBtnA1 {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

.landing-btn span:nth-child(2) {
  position: absolute;
  top: 0;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(to bottom, #0c002b, #2f96be);
  -webkit-animation: landingBtnA2 2s linear infinite;
          animation: landingBtnA2 2s linear infinite;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  box-shadow: 0 5px 10px #2f96be;
}

@-webkit-keyframes landingBtnA2 {
  0% {
    top: -100%;
  }
  100% {
    top: 100%;
  }
}

@keyframes landingBtnA2 {
  0% {
    top: -100%;
  }
  100% {
    top: 100%;
  }
}

.landing-btn span:nth-child(3) {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(to left, #0c002b, #2f96be);
  -webkit-animation: landingBtnA3 2s linear infinite;
          animation: landingBtnA3 2s linear infinite;
  box-shadow: -5px 0 10px #2f96be;
}

@-webkit-keyframes landingBtnA3 {
  0% {
    right: -100%;
  }
  100% {
    right: 100%;
  }
}

@keyframes landingBtnA3 {
  0% {
    right: -100%;
  }
  100% {
    right: 100%;
  }
}

.landing-btn span:nth-child(4) {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(to top, #0c002b, #2f96be);
  -webkit-animation: landingBtnA4 2s linear infinite;
          animation: landingBtnA4 2s linear infinite;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  box-shadow: 0 -5px 10px #2f96be;
}

@-webkit-keyframes landingBtnA4 {
  0% {
    bottom: -100%;
  }
  100% {
    bottom: 100%;
  }
}

@keyframes landingBtnA4 {
  0% {
    bottom: -100%;
  }
  100% {
    bottom: 100%;
  }
}

.landing-btn:hover > span {
  opacity: 0;
}

@media (min-width: 767px) {
  .landing .landing-title {
    right: 5.5rem;
  }
  .landing .landing-title h1,
  .landing .landing-title h3 {
