  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

a,
a:visited {
  color: #f5f5f5;
}

body {
  max-width: 100vw;
  overflow-x: hidden;
  background-color: #2f96be;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

body::-webkit-scrollbar {
  width: 1rem;
}

body::-webkit-scrollbar-track {
  background-image: linear-gradient(to top, #314d5c, #114e66);
}

body::-webkit-scrollbar-thumb {
  background-image: linear-gradient(to bottom, #688cb8, #1175f7);
  border-radius: 6px;
  border: 2px solid #000;
}

.App {
  text-align: center;
  width: 100%;
  position: relative;
  left: -2px;
}

.rotator {
  width: 100%;
  height: 100%;
  transform-origin: right top;
  transition: 400ms;
}
